//     
//   - Logo 
//    
//  -      UserNav(  backend      )
//  -       UserBar
//  -  LogOutBtn

import { useState } from 'react';
import { Link } from 'react-router-dom';
import MediaQuery from 'react-responsive';
import Logo from 'components/Logo';
import UserBar from 'components/UserBar';
import BurgerMenu from 'components/BurgerMenu';
import LogOutBtn from 'components/LogOutBtn';
import sprite from '../../images/sprite.svg';

import {
  IconBurgerMenuStyled,
  HeaderContainer,
  LinkSettingsStyled,
  NavLinkStyled,
  NavStyled,
  NavWrapper,
  WrapperNavLinkStyled,
} from './Header.styled';

//   useSelector  profile(  profile)    state.
//    NavWrapper,  profile === null ? null,     NavWrapper.

function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const toggleMenu = () => {
    setIsMenuOpen(prevState => !prevState);
  };

  return (
    <HeaderContainer>
      <NavStyled>
        <Link to="/">
          <Logo />
        </Link>

        <NavWrapper>
          <MediaQuery minWidth={1440}>
            <WrapperNavLinkStyled>
              <NavLinkStyled to="/diary">Diary</NavLinkStyled>
              <NavLinkStyled to="/products">Products</NavLinkStyled>
              <NavLinkStyled to="/exercises">Exercises</NavLinkStyled>
            </WrapperNavLinkStyled>
          </MediaQuery>

          <WrapperNavLinkStyled>
            <LinkSettingsStyled to="/profile">
              <UserBar />
            </LinkSettingsStyled>

            <MediaQuery minWidth={1440}>
              <LogOutBtn />
            </MediaQuery>

            <MediaQuery maxWidth={1439}>
              <IconBurgerMenuStyled onClick={toggleMenu}>
                <use href={`${sprite}#icon-burger-menu`} />
              </IconBurgerMenuStyled>
            </MediaQuery>
          </WrapperNavLinkStyled>
        </NavWrapper>
      </NavStyled>

      <MediaQuery maxWidth={1439}>
        {isMenuOpen && <BurgerMenu onClose={toggleMenu} />}
      </MediaQuery>
    </HeaderContainer>
  );
}

export default Header;
